@import "../../colors.scss";
@import "../../mixins.scss";


.eth {
  .ui.attached.tabular.menu {
    min-height: 0;
  }
  
  background: linear-gradient(to bottom, #9bacfe, #dbe0fa 300px);
  padding: 62px 0 64px;
  flex-grow: 1;

  &__header {
    @include header-bg(376px);
    background-image: url("../../public/images/header-eth.png");

    .ui .header {
      padding-top: 100px;
      font-size: 3.2rem;
      font-weight: bold;
      color: #463f94;
    }

    &-menu {
      display: flex;
      margin-top: 30px;
      justify-content: center;

      .ui.button {
        font-size: 0.9rem;
        font-weight: normal;
        margin: 0 3px;
      }
      .btn-active {
        background-color: #8c94eb !important;
        color: #fff !important;
      }
    }
    p {
      padding-top: 20px;
      font-weight: 600;
      font-size: 1.2rem;
    }
  }

  // MOBILE

  &__header-mobile {
    background-size: cover;
    background-position: 70%;
    min-height: 285px;

    .ui .header {
      font-size: 1.9rem;
      padding-top: 65px;
    }
  }

  &-mobile {
    padding: 20px 0 40px;
  }
}
